<template>
  <div class="home">
    <div class="header"></div>
    <div class="main">
      <div v-for="item in list" :key="item.id" class="content-box">
        <div class="list-header">
          <div class="header-loader"></div>
          <div class="">{{ item.title }}</div>
        </div>
        <div class="list-content">
          <div
            class="list-item"
            v-for="contentItem in item.content"
            :key="contentItem.id"
            @click="goUrl(contentItem)"
          >
            <div class="item-title">{{ contentItem.title }}</div>
            <div class="item-description">{{ contentItem.description }}</div>
            <div class="item-notes">
              <a-tag
                color="#4caf50"
                v-for="(note, index) in contentItem.notes"
                :key="index"
                >{{ note }}</a-tag
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
// @ is an alias to /src
import { ref, onMounted } from "vue";
import jsonList from "@/assets/json/list.json";
let list = ref([]);

onMounted(() => {
  getList();
});
const getList = () => {
  list.value = jsonList.list;
};
const goUrl = (item) => {
  console.log("地址", item.url);
  window.open(item.url, item.id);
};
</script>
<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .header {
    width: 100%;
    height: 50px;
    background-color: #000;
    flex-shrink: 0;
  }
  .main {
    flex: 1;
    margin: 0 auto;
    padding: 3.6rem 2rem 0;
    max-width: 1000px;
    width: 100%;

    .content-box {
      .list-header {
        text-align: left;
        padding: 20px 5px;
        font-size: 24px;
        color: #000;
        font-weight: 550;
        display: flex;
        align-items: center;
        .header-loader {
          width: 25px;
          aspect-ratio: 1;
          display: grid;
          margin-right: 10px;
        }
        .header-loader:before,
        .header-loader:after {
          content: "";
          grid-area: 1/1;
          width: 10px;
          aspect-ratio: 1;
          box-shadow: 0 0 0 2px #7bd77e inset;
          filter: drop-shadow(14px 14px 0 #7bd77e);
          animation: loadBox 2s infinite alternate;
        }
        .header-loader:after {
          margin: 0 0 0 auto;
          filter: drop-shadow(-14px 14px 0 #7bd77e);
          animation-delay: -1s;
        }
        @keyframes loadBox {
          0%,
          10% {
            border-radius: 0;
          }
          30%,
          40% {
            border-radius: 50% 0;
          }
          60%,
          70% {
            border-radius: 50%;
          }
          90%,
          100% {
            border-radius: 0 50%;
          }
        }
      }
      .list-content {
        width: 100%;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        .list-item {
          cursor: pointer;
          flex: 0 0 31.3%;
          min-width: 100px;
          background-color: #fbfbfb;
          border: 1px solid rgba(213, 212, 212, 0.2);
          margin-right: 2%;
          margin-bottom: 20px;
          position: relative;
          &:hover {
            box-shadow: 5px 10px 8px 0px rgba(0, 0, 0, 0.3);
          }
          .item-title {
            font-size: 24px;
            font-weight: bold;
            color: #fff;
          }
          .item-title {
            $f: 0.5em; /* control the folded part*/
            $r: 0.8em; /* control the ribbon shape */

            position: absolute;
            top: 10px;
            left: calc(-1 * $f);
            padding-inline: 0.25em;
            line-height: 1.8;
            background: #39943c;
            border-bottom: $f solid #0005;
            border-right: $r solid #0000;
            clip-path: polygon(
              calc(100% - $r) 0,
              0 0,
              0 calc(100% - $f),
              $f 100%,
              $f calc(100% - $f),
              calc(100% - $r) calc(100% - $f),
              100% calc(50% - $f / 2)
            );
          }
          .item-description {
            height: 50px;
            margin-top: 70px;
            font-size: 18px;
            color: #9f9f9f;
          }
          .item-notes {
            text-align: left;
            padding: 5px;
            background-color: #cceec234;
          }
        }
      }
    }
  }
}

@media (max-width: 750px) {
  .home {
    .main {
      .content-box {
        .list-content {
          .list-item {
            flex: 0 0 100%;
          }
        }
      }
    }
  }
}
</style>
